.shadow-cards{
  box-shadow:  0px -1px 4px 4px #ff8ffd;
  
}
.shadow-cards:hover{
  box-shadow:  0px -7px 5px 4px #ff8ffd; 
  scale: 102%;
}


.nav_link{
  position: relative;
  transition: all 0.2s;
  color: #c403ff;
  text-shadow:
      0 0 7px #f8abff,
      0 0 10px #ff8ffd,
      0 0 20px #ff8afb,
      0 0 30px #d48bff,   /* lighter purple */
      0 0 82px #e17eff,   /* main purple */
      0 0 92px #a84eff,   /* deeper purple */
      0 0 80px #e17eff,
      0 0 151px #9b61ff; 
  }  /* darkest shade */

.nav_link::after{
  content: '';
  height: 2px;
  width: 100%;
  background: #c98cf7;
  position: absolute;
  left: 0;
  opacity: 0;
  bottom: -6px;
  transition: ease-in-out 0.2s;
}

.nav_link:hover::after{
  opacity: 1;
  background: #d9a8ff;
  text-shadow:
      0 0 7px #f8abff,
      0 0 10px #ff8ffd,
      0 0 20px #ff8afb,
      0 0 30px #d48bff,   /* lighter purple */
      0 0 82px #e17eff,   /* main purple */
      0 0 92px #a84eff,   /* deeper purple */
      0 0 80px #e17eff,
      0 0 151px #9b61ff;
 }


